.home {
  max-width: 1024px;
  margin: auto;
}
.home > button {
  margin-top: 50px;
  cursor: pointer;
  padding: 13px 8px;
  border: none;
  color: white;
  font-weight: 500;
  font-size: 16px;
  border-radius: 5px;
  background-color: #0c79f6;
  margin-bottom: 30px;
}
@media (max-width: 768px) {
  
  .home{
    margin: 0px 15px;
  }
}